import React, { useContext } from "react"
import * as Styled from "./styled"
import { motion, useCycle } from "framer-motion"
import { ThemeContext } from "../../contexts/ThemeContext"

const ThemeSwitcher = (props) => {
    const { toggleTheme } = useContext(ThemeContext)

    // States for the button to cycle through

    const [deg, cycleDeg] = useCycle(0, 180)

    // Click handler

    const handleClick = () => {
        toggleTheme()
        cycleDeg()
    }
    

    return (
        <Styled.Wrapper invertRight={props.invertRight}>
            <button onClick={() => handleClick()}>
                <motion.div animate={{rotate: deg}}>
                    <svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 16 16"><path fillRule="evenodd" clipRule="evenodd" d="M8 16A8 8 0 108 0a8 8 0 000 16zm0-2A6 6 0 018 2v12z"/></svg>
                </motion.div>
            </button>
        </Styled.Wrapper>
    )
}

export default ThemeSwitcher
