import { createGlobalStyle } from "styled-components"

const GlobalStyles = createGlobalStyle`

    // Variables

    :root {

        // Font families

        --family-heading: "Epilogue", sans-serif;
        --family-body: "Bitter", sans-serif;

        // Line heights

        --line-height-tight: 1;
        --line-height-heading: 1.4;
        --line-height-paragraph: 2.0;
        --line-width: 1.5px;

        // Spacing units
        
        --su: 32px;
        --nav-height: 102px;

        @media (min-width: 800px) {
            --su: 80px;
            --nav-height: 206px;
        }

        @media (min-width: 1200px) {
            --su: 104px;
        }

        @media (min-width: 1600px) {
            --su: 128px;
        }

        @media (min-width: 2000px) {
            --su: 160px;
        }

        // Element sizes

        --article-max-width: 800px;

        @media (min-width: 2000px) {
            --article-max-width: 960px;
        }

    }

    // Reset and adjusting defaults

    *,
    *:after,
    *:before {
        box-sizing: border-box;
        margin: 0;
        padding: 0;
    }

    img {
        display: block;
        width: 100%;
        max-width: 100%;
    }

    svg {
        display: block;
    }

    video {
        display: block;
        width: 100%;
    }

    // Base page styles

    body {
        background-color: ${props => props.theme.background.normal};
        color: ${props => props.theme.text.normal};
        font-family: var(--family-heading);
        font-weight: 400;
        line-height: var(--line-height-heading);
        
    }

    // Base typography styles
    
    a,
    a:visited {
        color: ${props => props.theme.link.normal};
        text-decoration: none;
    }

    a:hover {
        color: ${props => props.theme.link.hover};
    }
    
    h1 {
        font-size: calc(64px + (80 - 64) * ((100vw - 400px) / (800 - 400)));
        font-weight: 800;
        hyphens: auto;
        line-height: var(--line-height-tight);
        max-width: 1600px;
        word-break: break-all;

        @media screen and (min-width: 800px) {
            font-size: calc(96px + (200 - 96) * ((100vw - 800px) / (2000 - 800)));
        }

        @media screen and (min-width: 2000px) {
            font-size: 200px;
        }

    }

    h2 {
        font-size: 56px;
        font-weight: 800;
        line-height: var(--line-height-tight);

        @media (min-width: 800px) {
            font-size: 80px;
        }

    }

    h3 {
        font-size: 24px;
        font-weight: 800;
        line-height: var(--line-height-heading);

        @media (min-width: 800px) {
            font-size: 32px;
        }

        @media (min-width: 2000px) {
            font-size: 36px;
        }

    }

    h4 {
        font-size: 18px;
        font-weight: 600;
        line-height: var(--line-height-heading);

        @media (min-width: 800px) {
            font-size: 24px;
        }

        @media (min-width: 2000px) {
            font-size: 26px;
        }

    }

    h5 {
        font-size: 14px;
        font-weight: 600;
        line-height: var(--line-height-heading);

        @media (min-width: 800px) {
            font-size: 18px;
        }

        @media (min-width: 2000px) {
            font-size: 20px;
        }

    }

    h6 {
        font-size: 11px;
        font-weight: 400;
        letter-spacing: 0.08em;
        line-height: var(--line-height-tight);
        text-transform: uppercase;

        @media (min-width: 800px) {
            font-size: 14px;
        }

        @media (min-width: 2000px) {
            font-size: 16px;
        }

    }

    p {
        font-family: var(--family-body);
        font-size: 18px;
        font-weight: 400;
        line-height: var(--line-height-paragraph);

        @media (min-width: 800px) {
            font-size: 24px;
        }

        @media (min-width: 2000px) {
            font-size: 26px;
        }

    }

    button {
        background: none;
        border-style: none;
        box-sizing: content-box;
        cursor: pointer;
        display: inline;
        font: inherit;
        height: auto;
        line-height: inherit;
        padding: 0;
        perspective-origin: 0 0;
        text-align: start;
        transform-origin: 0 0;
        width: auto;
    }

    blockquote {
        border-left: 1px solid ${props => props.theme.line.normal};
        padding: 8px 32px;
        font-size: 32px;
        line-height: var(--line-height-paragraph);
    }

    pre {
        background: ${props => props.theme.code.background};
        border-radius: 4px;
        color: ${props => props.theme.code.text};
        padding: 24px;
        font-size: 13px;

        @media (min-width: 800px) {
            font-size: 18px;
            padding: 32px;
        }

    }

    code {
        background: ${props => props.theme.code.background};
        border-radius: 4px;
        color: ${props => props.theme.code.text};
        font-size: 13px;

        @media (min-width: 800px) {
            font-size: 18px;
        }


    }

    // Generic .has-shadow class to place a shadow on any element

    .has-shadow {
        box-shadow: 0 32px 32px rgba(0,0,0,0.05), 0 16px 16px rgba(0,0,0,0.05), 0 8px 8px rgba(0,0,0,0.05), 0 4px 4px rgba(0,0,0,0.05), 0 0 2px rgba(0,0,0,0.2);
    }

`
export default GlobalStyles