import React, { useContext, useRef, useState } from "react"
import * as Styled from "./styled"
import { Link } from "gatsby"
import { AnimatePresence } from "framer-motion"
import { NavContext } from "../../contexts/NavContext"
import { Fade } from "../Animations"
import EmailLink from "../EmailLink"
import Logo from "../Logo"
import NavMenu from "../NavMenu"
import ThemeSwitcher from "../ThemeSwitcher"

export default () => {

    // Nav color scheme context

    const { pageNavScheme, visibleNavScheme, updateVisibleNavScheme } = useContext(NavContext)

    // Menu state

    const [menuOpen, setMenuOpen] = useState(false)

    // Toggle menu visibility

    const hideMenu = () => {
        setMenuOpen(false)
        setTimeout(() => {
            updateVisibleNavScheme(pageNavScheme.invertLeft, pageNavScheme.invertRight)
        }, 300)    
}

    const showMenu = () => {
        setMenuOpen(true)
        setTimeout(() => {
            updateVisibleNavScheme(true, true)
        }, 200)    
    }

    const toggleMenu = () => {
        menuOpen ? hideMenu() : showMenu()
    }

    // Ref passed to NavMenu component used to listen for clicks outside of the Nav component

    const navRef = useRef()

    return (
        <>
            <Styled.Wrapper ref={navRef}>
                <Styled.Left invertLeft={visibleNavScheme.invertLeft}>
                    <Fade delay={0.5}>
                        <Link onClick={() => hideMenu()} to="/">
                            <Logo invertLeft={visibleNavScheme.invertLeft} />
                        </Link>
                    </Fade>
                </Styled.Left>
                <Styled.Right invertRight={visibleNavScheme.invertRight}>
                    <h5>
                        <Fade delay={0.5}>
                            <button onClick={() => toggleMenu()}>Work</button>
                        </Fade>
                    </h5>
                    <h5>
                        <Fade delay={0.5}>
                            <Link onClick={() => hideMenu()} to="/info">Info</Link>
                        </Fade>
                    </h5>
                    <h5>
                        <Fade delay={0.5}>
                            <a href="https://twitter.com/aaronshapiro">Twitter</a>
                        </Fade>
                    </h5>
                    <h5>
                        <Fade delay={0.5}>
                            <EmailLink />
                        </Fade>
                    </h5>
                    <div>
                        <Fade delay={0.5}>
                            <ThemeSwitcher invertRight={visibleNavScheme.invertRight} />
                        </Fade>
                    </div>
                </Styled.Right>
                <AnimatePresence>
                    { menuOpen && <NavMenu menuOpen={menuOpen} hideMenu={hideMenu} navRef={navRef}/> }
                </AnimatePresence>
            </Styled.Wrapper>
        </>
    )
}